import React, { useState } from 'react';
import Menu from '../Menu/Menu'
import Cards from '../Cards/Cards'
import { Articles, SaveNewArticle } from '../../types/Articles';
import AddIcon from '@material-ui/icons/Add';
import { makeStyles } from '@material-ui/core/styles';
import { CssBaseline, Fab, Tooltip } from '@material-ui/core';
import { ModalWindow } from '../Modal/Modal';


const useStyles = makeStyles({
  btn: {
    position: 'fixed',
    bottom: '20px',
    right: '30px',
  },
})

const Reader = ({login, role, articles, isAuthor, authorId, postArticle}:ownProps) => {  
  const classes = useStyles()

  const [open, setOpen] = useState(false);

  const handleModal = () => {
    setOpen(!open)
  }

  const onSave = ({title, descr}:SaveNewArticle) => {
    postArticle({title, descr, authorId})
    setOpen(false)
  }

  const cards = articles.filter(el => el.author.login === login)

  return (
    <div>
      <CssBaseline />
      <Menu login={login} role={role}/>
      <div>
        {cards.map(mag => {
          return <div className='container' key={mag.id}>
            <Cards
              descr={mag.descr}
              name={mag.title}
              id={mag.id}
              author={mag.author}
              login={login}
              stage={mag.stage}
            />
          </div>
        })}
      </div>
      {isAuthor &&
        <Tooltip onClick={handleModal} title="Add" aria-label="add" className={classes.btn}>
          <Fab color="primary" >
            <AddIcon />
          </Fab>
        </Tooltip>
      }
      {open &&
        <ModalWindow 
          handleModal={handleModal}
          open={open}
          onSave={onSave}
        />
      }
    </div>
  );
}


export default Reader

type ownProps = {
  login: string | null
  role: string| null
  articles: Array<Articles>
  isAuthor: boolean,
  authorId: string | null
  postArticle: (value:SaveNewArticle) => void
}